.p {
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(243, 242, 242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
    transition: all .2s ease-in-out;
  }

  .p:hover {
    transform: scale(1.1);
  }
  
  .p-browser {
    height: 20px;
    background-color: #FF7217;
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
  }
  
  .p-circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    margin-left: 4px;
    background-color: white;
  }
  
  .p-img {
    width: 100%;
    transition: all 10s ease;
  }
  
  .p:hover .p-img {
    transform: translateY(-50%);
  }
  
  @media screen and (max-width: 800px) {
    .p {
      width: 400px;
      justify-content: center;
      align-items: center;

    }
  }